  import React from 'react';
  import { Line } from 'react-chartjs-2';
  
  //let data: any;
  const LineChart = (props) => {
    
    let labels = props.dateArray; 
    let high = props.highP;
    let low = props.lowP;
    let pulzz = props.pulz

    //console.log(...pulzz)
    //console.log(Array.isArray(pulzz))
    //console.log("pulz graph: " + pulzz)
    let minValue; //= 30//Math.min(...pulzz) - 10;
    let maxValue; //= 200//Math.max( ...props.highP ) + 10;
    if (props.pulz) {
      if (props.highP) {
        minValue = Math.min(...props.pulz) - 10;
        maxValue = Math.max( ...props.highP ) + 10;
      }
    }
    console.log(minValue)
    console.log(maxValue)
    // options for LINECHART !!
    const options = {
              responsive: true,
              tooltips: {
                titleFontSize: 12,
                bodyFontSize: 12
              },
              title:{
                display: true,
                text: 'Pritisk in pulz',
                fontColor: '#009fd9'
              },
              legend: {
                display: true,
                position: 'bottom',
                labels: {
                  fontColor: '#009fd9'
                }
              },
              scales: {
                yAxes: [{
                  display: true,
                  position: 'right',
                  gridLines: {
                    display: false,
                  },
                  ticks: {
                    suggestedMax: maxValue,
                    suggestedMin: minValue,
                    stepSize: 10,
                    fontColor: '#009fd9'
                }
                }],
                xAxes: [{
                  gridLines: {
                    display: false,
                 },
                  ticks: {
                    display: false, // doesnt show XAXIS 
                    scaleLabel: {
                      display: true,
                  },
                  fontColor: '#009fd9',
                  autoSkip: true,
                  }
                }]
              }
    }

    let highPreasure = {
      fill: true,
      //backgroundColor: 'rgba(158, 215, 101, 0.3)',
      //borderColor: 'rgb(158, 215, 101, 0.5)',
      //backgroundColor: 'rgb(255, 255, 255, 0.3)',
      //borderColor: 'rgb(255, 255, 255, 0.8)',
      backgroundColor: 'rgb(56, 128, 255, 0.3)',
      borderColor: 'rgb(56, 128, 255, 0.8)',
      label: 'Visok',
      borderWidth: 1,
      data: high
    };
    let lowPreasure = {
      fill: true,
      //lineTension: 0.5,
      //backgroundColor: 'rgb(243, 188, 138, 0.3)',
      //borderColor: 'rgb(243, 188, 138, 0.5)',
      //backgroundColor: 'rgb(255, 255, 255, 0.3)',
      //borderColor: 'rgb(255, 255, 255, 0.8)',
      backgroundColor: 'rgb(56, 128, 255, 0.3)',
      borderColor: 'rgb(56, 128, 255, 0.8)',
      borderWidth: 1,
      label: 'Nizek',
      data: low
    }

    let pulzData = {
      fill: true,
      //lineTension: 0.5,
      //backgroundColor: 'rgb(226, 102, 108, 0.3)',
      //borderColor: 'rgb(226, 102, 108, 0.5)',
      //backgroundColor: 'rgb(255, 255, 255, 0.3)',
      //borderColor: 'rgb(255, 255, 255, 0.8)',
      backgroundColor: 'rgb(56, 128, 255, 0.3)',
      borderColor: 'rgb(56, 128, 255, 0.8)',
      borderWidth: 1,
      label: 'Pulz',
      data: pulzz
    }
    //DATA for LINECHART !!
    let data = {
      labels: labels,
      datasets: [lowPreasure, highPreasure, pulzData]
    }
// GRAPH WORKING - FIX DATE INPUT  

    return  <Line data={data} options={options}/> 
}

  export default LineChart;