
import React, { useState, useEffect, useRef } from 'react';
import moment from 'moment';
import LineChart from './Graph/LineChartGlucose';
import styles from '../../styles/ShareGraphs.module.css';
import * as firebase from 'firebase';
  
const Glucoze = (props) => {

  let userId = props.data // getting UID

  // arrey with object with {id, date, glucose}
  const [allGlucose, setAllGlucose] = useState(Array); 
  
  const [glucoseArray, setGlucoseArray] = useState(Array); // setting array of glucose
  const [dateArray, setDateArray] = useState(Array); // setting array of dates
  
  // *
  // best way to check for mounted component !!
  const isCurrent = useRef(true);

  useEffect(() => {
    return () => {
      isCurrent.current = false;
    }
  }, [])
  // * 
  // then use 
  // if (isCurrent.current) { setState(XXYY) }  !!
  // *

  const getDataFirebase = (userId) => {
    //let array: any;
    let data;
    console.log("checking how many times it renders !!")
    // getting data from location for WEIGHT
    let getData = firebase.database().ref('/users/' + userId + '/glucose');
    getData.on('value', function(snapshot) {
      //console.log(snapshot.val());
      data = snapshot.val();
      if (data !== null) {
        // getting data to correct form ID:, DATE:, WEIGHT:
        const allGlucoseArray = Object.keys(data).map( key => {
          return  { 
            id: key, 
            date: data[key].date, 
            glucose: data[key].glucose 
          };
        })
        // set in reverse order so the last input is 1 in line
        // checking for componenet is mounted !!
        if (isCurrent.current) { setAllGlucose(allGlucoseArray.reverse()) }
        //console.log(allGlucoseArray)
        let testDate = [];
        //const dateArray = 
        allGlucoseArray.map( each => {
          return testDate.push(each.date)
        })
        console.log("datearray?: " + testDate)
        if (isCurrent.current) { setDateArray(testDate.reverse()) }
        let testGlucose = [];
        //const weightArray = 
        allGlucoseArray.map( each => {
          return testGlucose.push(each.glucose)
        })
        console.log("weightarray?: " + testGlucose)
        if (isCurrent.current) { setGlucoseArray(testGlucose.reverse()) }
      }
    })
  }

  useEffect(() => {
    getDataFirebase(userId)  //user.uid);
  }, [userId])

  useEffect(() => {
    console.log('rendering allWeights(id, glucose, date)', allGlucose)
  }, [allGlucose]);
  useEffect(() => {
    console.log('rendering weightsArray(only glucose)', glucoseArray)
  }, [glucoseArray]);
  useEffect(() => {
    console.log('rendering dateArray(only dates)', dateArray)
  }, [dateArray]);
  
  // handling buttons under graph - showing ALL, WEEK, MONTH
    
  const handleAll = () => {
    let testAllDate = [];
    let testAllWeight = [];
    let thisAllArray = [];

    allGlucose.forEach( (glucose) => {
        thisAllArray.push(glucose);
        testAllDate.push(glucose.date);
        testAllWeight.push(glucose.glucose)
    })
    if (isCurrent.current) { setDateArray(testAllDate.reverse()) }
    if (isCurrent.current) { setGlucoseArray(testAllWeight.reverse()) }
    
    console.log("rendering all data")
  }
  const handleMonth = () => {
    let testMonthDate = [];
    let testMonthWeight = [];
    let thisMonthArray = [];
    let thisMonthStart = moment().subtract(1, 'month').format("YYYY/MM/DD");
    
    allGlucose.forEach( (glucose) => {
      if (moment(glucose.date).isSameOrAfter(thisMonthStart)) {
        thisMonthArray.push(glucose);
        testMonthDate.push(glucose.date);
        testMonthWeight.push(glucose.glucose)
      }
    })
    if (isCurrent.current) { setDateArray(testMonthDate.reverse()) }
    if (isCurrent.current) { setGlucoseArray(testMonthWeight.reverse()) }
      
    console.log("Month");
  }
  const handleWeek = () => {
    let testWeekDate = [];
    let testWeekWeight = [];
    let thisWeekArray = [];
    let thisWeekStart = moment().subtract(1, 'week').format("YYYY/MM/DD");
    
    allGlucose.forEach( (glucose) => {
      if (moment(glucose.date).isSameOrAfter(thisWeekStart)) {
        thisWeekArray.push(glucose);
        testWeekDate.push(glucose.date);
        testWeekWeight.push(glucose.glucose)
      }
    })
    if (isCurrent.current) { setDateArray(testWeekDate.reverse()) }
    if (isCurrent.current) { setGlucoseArray(testWeekWeight.reverse()) }
      
    console.log("week")
  }

  return (
    <div className={styles.Desktop} >
      <div className={styles.BeforeChart}>
        <LineChart 
          dateArray={dateArray} 
          glucoseArray={glucoseArray}/>
      </div>
        {/*   TESTING 3 buttons for WEEK, MONTH, ALL*/}
      <div className={styles.InChart}>
        <div
          onClick={() => handleWeek()} 
          className={styles.InChartButton}>teden</div>
        <div 
          onClick={() => handleMonth()}
          className={styles.InChartButton}>mesec</div>
        <div 
          onClick={() => handleAll()}
          className={styles.InChartButton}>skupaj</div>
      </div>
      
    </div>
  );
};

export default Glucoze;